function EventDetail({ event, onClose }) {
  const [thumbsSwiper, setThumbsSwiper] = useState(null);

  // Configuraci√≥n memoizada del Swiper principal
  const mainSwiperConfig = useMemo(() => ({
    modules: [Autoplay, Navigation, Thumbs],
    spaceBetween: 10,
    navigation: true,
    autoplay: { delay: 5000, disableOnInteraction: false },
    thumbs: { swiper: thumbsSwiper }
  }), [thumbsSwiper]);

  // Configuraci√≥n memoizada del Swiper de thumbs
  const thumbsSwiperConfig = useMemo(() => ({
    modules: [Thumbs],
    watchSlidesProgress: true,
    spaceBetween: 10,
    slidesPerView: 4,
    freeMode: true
  }), []);

  const handleShare = useCallback(() => {
    const fullText = `*${event.title}*\n\n` +
      `üóìÔ∏è D√≠a: ${event.day}\n` +
      `‚è∞ Horario: ${event.time}\n` +
      `üë• Edades: ${EVENT_TARGETS[event.id]}\n\n` +
      `*Descripci√≥n:*\n${EVENT_DESCRIPTIONS[event.id].join('\n\n')}\n\n` +
      `üìç Ubicaci√≥n: Colegiales 4950\n\n` +
      `üîó M√°s informaci√≥n: ${window.location.href}\n\n` +
      `¬°Te esperamos! üôå`;

    const encodedText = encodeURIComponent(fullText)
      .replace(/%F0%9F%93%96/g, 'üìñ')
      // ... (resto de reemplazos de emojis)
      .replace(/%F0%9F%8D%AC/g, 'üç¨');

    window.open(`https://wa.me/?text=${encodedText}`, '_blank');
  }, [event]);

  return (
    <div className="event-detail-overlay">
      <div className="event-detail-container">
        <button className="event-detail-close" onClick={onClose}>
          <i className="bi bi-x-lg"></i>
        </button>

        <div className="event-detail-content">
          <div className="event-detail-gallery">
            <Swiper {...mainSwiperConfig} className="event-detail-main-slider">
              {EVENT_GALLERIES[event.id].map((img, index) => (
                <SwiperSlide key={index} >
                  <div
                    className="event-detail-slide-img "
                    style={{ backgroundImage: `url(${img.image})` }}
                  ></div>
                </SwiperSlide>
              ))}
            </Swiper>

            <Swiper
              {...thumbsSwiperConfig}
              onSwiper={setThumbsSwiper}
              className="event-detail-thumbs"
              style={{padding:"10px"}}
            >
              {EVENT_GALLERIES[event.id].map((img, index) => (
                <SwiperSlide key={index}>
                  <div
                    className="event-detail-thumb-img"
                    style={{ backgroundImage: `url(${img.image})` }}
                  />
                </SwiperSlide>
              ))}
            </Swiper>
          </div>

          <div className="event-detail-info">
            <div className="event-detail-header">
              <span className="event-detail-day">{event.day}</span>
              <h2 className="event-detail-title mb-1">{event.title}</h2>
              <div className="event-detail-time mb-1">
                <i className="bi bi-clock-fill mb-1"></i> {event.time}
              </div>
            </div>

            <div className="event-detail-description mt-1 mb-0">
              {EVENT_DESCRIPTIONS[event.id].map((paragraph, index) => (
                paragraph ? <p className='mb-0' key={index}>{paragraph}</p> : <br key={index} />
              ))}

              <div className="event-detail-features my-0 mt-2">
                <div className="event-feature">
                  <i className="bi bi-people-fill"></i>
                  <span>{EVENT_TARGETS[event.id]}</span>
                </div>

                <button className="btn btn-secondary" onClick={handleShare}>
                  <i className="bi bi-share-fill"></i> Compartir
                </button>
              </div>
            </div>
          </div>
        </div>
      </div>
    </div>
  );
}

export default React.memo(EventDetail);